export const removeAllCharactersExceptLetters = (word) => 
  word.replace(/[^a-z]/g, '');

export const removePunctuation = (word) => 
  word.replace(/[-]/g, '');
  
export const cleanseWord = (word) => {
  if (!word) {
    return '';
  }

  return removeAllCharactersExceptLetters(removePunctuation(word.toLowerCase()));
}
